.name-tag {
  font-size: .8rem; }
  .name-tag__portrait {
    border-radius: 100%; }

.navigation .navigation-animatable {
  -webkit-transform: translateX(-120vw);
          transform: translateX(-120vw); }
  .navigation .navigation-animatable.flip-x {
    -webkit-transform: translateX(-120vw) scaleX(-1);
            transform: translateX(-120vw) scaleX(-1); }

.slide-in {
  -webkit-animation: slide-in-x-animation .5s forwards;
          animation: slide-in-x-animation .5s forwards; }

.slide-in-flip-x {
