.hero {
  height: 55vh; }
  .hero__bg {
    background-image: url(/static/media/sun-sky.742a6cd9.svg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%; }
  .hero__overlay {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0)), color-stop(rgba(255, 255, 255, 0)), to(white));
    background: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0), white); }


.curly {
  stroke: black;
  stroke-width: 10px;
  stroke-linejoin: round;
  stroke-linecap: round; }
  .curly--header {
    stroke-dasharray: 1427px;
    stroke-dashoffset: 1427px;
    -webkit-transition: stroke-dashoffset .5s;
    transition: stroke-dashoffset .5s; }
  .curly--nav {
    -webkit-transition: stroke-dashoffset .5s;
    transition: stroke-dashoffset .5s;
    stroke-dasharray: 1233px;
    stroke-dashoffset: 1233px; }
  .curly--tree {
    stroke-width: 20px;
    -webkit-transition: stroke-dashoffset 1s;
    transition: stroke-dashoffset 1s;
    stroke-dasharray: 20905px;
    stroke-dashoffset: 20905px; }
    .curly--tree.mobile {
      stroke-width: 30px;
      stroke-dasharray: 15333px;
      stroke-dashoffset: 15333px; }
    .curly--tree.dash-in {
      -webkit-transition: stroke-dashoffset 1s;
      transition: stroke-dashoffset 1s; }
  .curly.dash-in {
    stroke-dashoffset: 0; }

.flip-x {
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1); }

.projects {
  padding-bottom: 5rem; }
  .projects .tree {
    width: 100%;
    height: auto; }

.project__image {
  -webkit-transform: translateX(-150vw);
          transform: translateX(-150vw); }
